@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat';
  background-color: #fafafa;
}
.wgs-main {
  padding: 1rem;
}
a {
  text-decoration: none;
}

.wgs-header{position:relative;width:100%;height:100vh;color:white;display:flex;justify-content:center;align-items:center;flex-direction:column;-webkit-clip-path:polygon(0 0, 0 calc(100% - 128px), 25% 100%, 100% calc(100% - 128px), 100% 0);clip-path:polygon(0 0, 0 calc(100% - 128px), 25% 100%, 100% calc(100% - 128px), 100% 0);background-size:cover, cover}.wgs-header h3{font-size:1.75rem;margin:0;font-style:italic}.wgs-header-title-box{display:flex;flex-direction:row;justify-content:center;align-items:center}.wgs-header-title-box h1{font-size:4rem;margin:0}@media (max-width: 644px){.wgs-header-title-box h1{font-size:2.5rem}}.wgs-header-title-box img{height:6rem;margin-right:1rem}@media (max-width: 644px){.wgs-header-title-box{flex-direction:column}}.wgs-header-links{position:fixed;top:0;left:0;width:100%;display:flex;justify-content:center;background:linear-gradient(90deg, rgba(24,174,232,0.75), rgba(19,198,163,0.75));z-index:1}.wgs-header-links a{padding:1rem;cursor:pointer;color:white}@supports ((-webkit-backdrop-filter: blur(8px)) or (backdrop-filter: blur(8px))){.wgs-header-links{background:linear-gradient(90deg, rgba(24,174,232,0.5), rgba(19,198,163,0.5));backdrop-filter:blur(8px);-webkit-backdrop-filter:blur(8px)}}

.wgs-section-title{font-size:3rem}.wgs-section-horizontal-row{box-sizing:border-box;position:relative;white-space:nowrap;width:100%;overflow:auto;padding:1rem}.wgs-tile{display:inline-flex;flex-direction:row;width:384px;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);color:black;background:white;transition:box-shadow 0.2s, -webkit-transform 0.2s;transition:transform 0.2s, box-shadow 0.2s;transition:transform 0.2s, box-shadow 0.2s, -webkit-transform 0.2s}.wgs-tile .wgs-tile-icon{width:128px;height:128px}.wgs-tile .wgs-tile-body{box-sizing:border-box;padding:1rem;width:256px;display:flex;flex-direction:column;white-space:normal}.wgs-tile .wgs-tile-body h2{margin:0}.wgs-tile:hover{-webkit-transform:translateY(-2px);transform:translateY(-2px);box-shadow:0 20px 25px -5px rgba(0,0,0,0.1),0 10px 10px -5px rgba(0,0,0,0.04)}

.wgs-ig-post{position:relative;height:256px;display:inline-block;overflow:hidden;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);transition:box-shadow 0.2s, -webkit-transform 0.2s;transition:transform 0.2s, box-shadow 0.2s;transition:transform 0.2s, box-shadow 0.2s, -webkit-transform 0.2s;margin-right:1rem;border-radius:1rem}.wgs-ig-post:hover{-webkit-transform:translateY(-2px);transform:translateY(-2px);box-shadow:0 20px 25px -5px rgba(0,0,0,0.1),0 10px 10px -5px rgba(0,0,0,0.04)}.wgs-ig-post:hover .wgs-ig-post-image{-webkit-filter:blur(4px) brightness(0.5);filter:blur(4px) brightness(0.5);-webkit-transform:scale(1.25) rotate(10deg);transform:scale(1.25) rotate(10deg)}.wgs-ig-post:hover .wgs-ig-post-bio{opacity:1}.wgs-ig-post-image{position:relative;top:0;left:0;height:100%;transition:-webkit-filter 0.2s, -webkit-transform 0.2s;transition:filter 0.2s, transform 0.2s;transition:filter 0.2s, transform 0.2s, -webkit-filter 0.2s, -webkit-transform 0.2s}.wgs-ig-post-bio{box-sizing:border-box;padding:1rem;margin:0;text-align:justify;line-height:1.5;position:absolute;top:0;left:0;opacity:0;width:100%;height:100%;white-space:normal;color:white;transition:opacity 0.2s;overflow-x:hidden;overflow-y:auto}

